import { FC, useEffect, useRef, useState } from 'react';
import styled, { css } from 'styled-components';

import {
  SummaryListItem,
  defaultThemeProp,
  Flex,
  Progress,
  useElement,
  StyledProgressRing,
  Count,
  Modal,
  SummaryListItems
} from '@pega/cosmos-react-core';

export interface FeedModalListProps {
  /** The count for the list of items. */
  count: number;
  /** The heading for the Modal. */
  heading: string;
  /** The list of MenuItems to render in the Modal. */
  listItems: SummaryListItem[];
  /** A callback that is triggered when the bottom of the Modal list is scrolled into view so that additional data may be displayed. */
  onLoadMore?: () => void;
}

const StyledLoadMore = styled.div<{ visible: boolean }>(({ theme, visible }) => {
  return css`
    ${visible &&
    css`
      margin-top: calc(2 * ${theme.base.spacing});
    `}

    ${StyledProgressRing} {
      min-height: 0;
      min-width: 0;

      ${visible &&
      css`
        min-height: 2rem;
        min-width: 2rem;
      `}
    }
  `;
});

StyledLoadMore.defaultProps = defaultThemeProp;

const FeedModalList: FC<FeedModalListProps> = ({
  count,
  heading,
  listItems,
  onLoadMore
}: FeedModalListProps) => {
  const [loadMoreEl, setLoadMoreEl] = useElement<HTMLDivElement>();
  const [listOverflows, setListOverflows] = useState(false);
  const listOverflowsRef = useRef(false);

  useEffect(() => {
    if (loadMoreEl) {
      const intersectionObserver = new IntersectionObserver(entries => {
        if (!entries[0].isIntersecting) {
          setListOverflows(true);
          listOverflowsRef.current = true;
        } else if (entries[0].isIntersecting && listOverflowsRef.current) {
          onLoadMore?.();
        }
      });

      intersectionObserver.observe(loadMoreEl);

      return () => {
        intersectionObserver.disconnect();
      };
    }
  }, [loadMoreEl, listOverflowsRef.current]);

  return (
    <Modal
      heading={
        <Flex container={{ gap: 1 }}>
          {heading}
          <Flex item={{ alignSelf: 'center' }} as={Count}>
            {count}
          </Flex>
        </Flex>
      }
    >
      <SummaryListItems items={listItems} />

      {onLoadMore && (
        <Flex
          ref={setLoadMoreEl}
          as={StyledLoadMore}
          visible={listOverflows}
          container={{ justify: 'center' }}
        >
          <Progress placement='inline' />
        </Flex>
      )}
    </Modal>
  );
};

export default FeedModalList;
