import { forwardRef, PropsWithoutRef, FC, Ref, ReactElement, useMemo, ReactNode } from 'react';
import styled, { css } from 'styled-components';

import Button from '../Button';
import Icon from '../Icon';
import MenuButton from '../MenuButton';
import MetaList from '../MetaList';
import SummaryItem from '../SummaryItem';
import Progress from '../Progress';
import Text from '../Text';
import { ForwardProps, BaseProps } from '../../types';
import { defaultThemeProp } from '../../theme';
import { useI18n } from '../../hooks';

import { getKindFromMimeType, getMimeTypeFromFile } from './utils';
import FileVisual from './FileVisual';

export interface FileUploadItemProps extends BaseProps {
  /** Name of the file. */
  name: string;
  /** A FileUploadVisual element to represent the file. */
  visual?: ReactElement;
  /** Additional information about the file. If progress prop is passed and its value is less than 100, this region is instead used for the upload progress indicator. */
  meta?: string | string[];
  /** When passed, will render a single icon button for yet to be or in progress uploads. Use this action to trigger xhr request termination. */
  onCancel?: (name: FileUploadItemProps['name']) => void;
  /** When passed, will render a single icon button or within a MenuButton if other actions are defined. */
  onEdit?: (name: FileUploadItemProps['name']) => void;
  /** When passed, will render a single icon button or within a MenuButton if other actions are defined. */
  onPreview?: (name: FileUploadItemProps['name']) => void;
  /** When passed, will render a single icon button or within a MenuButton if other actions are defined. */
  onDownload?: (name: FileUploadItemProps['name']) => void;
  /** When passed, will render a single icon button or within a MenuButton if other actions are defined. */
  onDelete?: (name: FileUploadItemProps['name']) => void;
  /**
   * Set if there is an error uploading or processing the file. Applies error styling to the meta string passed.
   * @default false
   */
  error?: boolean;
  /** A string to be used as an image src for a attachment thumbnail. Falls back to a provided icon or the default attachment icon. */
  thumbnail?: string;
  /** If the attachment is in an upload state, pass progress as a number between 0-100 representing the percentage of progress. */
  progress?: number;
  /** A ref to the file item's HTMLLIElement. */
  ref?: Ref<HTMLLIElement>;
}

interface FileAction {
  fn: (name: FileUploadItemProps['name']) => void;
  text: string;
  icon: string;
}

const StyledFileUploadItem = styled.li(props => {
  const { theme } = props;

  return css`
    border: 0.0625rem solid ${theme.base.palette['border-line']};
    background-color: ${theme.base.palette['primary-background']};
    padding: calc(0.5 * ${theme.base.spacing});
  `;
});

StyledFileUploadItem.defaultProps = defaultThemeProp;

const FileUploadItem: FC<FileUploadItemProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<FileUploadItemProps>, ref: FileUploadItemProps['ref']) => {
    const {
      name,
      meta,
      error = false,
      progress,
      thumbnail,
      onCancel,
      onEdit,
      onPreview,
      onDownload,
      onDelete,
      ...restProps
    } = props;

    const t = useI18n();

    const definedActions: FileAction[] = useMemo(
      () =>
        [
          { fn: onCancel, text: t('cancel'), icon: 'times' },
          { fn: onEdit, text: t('edit'), icon: 'pencil' },
          { fn: onPreview, text: t('preview'), icon: 'eye' },
          { fn: onDownload, text: t('download'), icon: 'download' },
          { fn: onDelete, text: t('delete'), icon: 'trash' }
        ].filter<FileAction>((action): action is FileAction => !!action.fn),
      [onCancel, onEdit, onPreview, onDownload, onDelete]
    );

    let metaContent: ReactNode = meta;
    let actions: ReactElement | undefined;

    if (typeof progress === 'number' && progress < 100) {
      metaContent = `${t('uploading')}${Math.floor(progress)}%`;
    }

    if (Array.isArray(metaContent)) {
      metaContent = (
        <MetaList
          items={metaContent.map(item => (
            <Text key={item?.toString()} variant='secondary' status={error ? 'error' : undefined}>
              {item}
            </Text>
          ))}
        />
      );
    } else if (typeof metaContent === 'string') {
      metaContent = (
        <Text variant='secondary' status={error ? 'error' : undefined}>
          {metaContent}
        </Text>
      );
    }

    if (definedActions.length === 1) {
      const action = definedActions[0];

      actions = (
        <Button
          icon
          variant='simple'
          onClick={() => action.fn?.(name)}
          aria-label={`${action.text} ${name}`}
        >
          <Icon name={action.icon} />
        </Button>
      );
    } else if (definedActions.length > 1) {
      actions = (
        <MenuButton
          text={t('actions')}
          iconOnly
          icon='more'
          variant='simple'
          menu={{
            items: definedActions.map(action => ({
              id: action.text,
              primary: action.text,
              onClick: () => {
                action.fn(name);
              }
            }))
          }}
        />
      );
    }

    const visual = useMemo(() => {
      if (typeof progress === 'number' && progress !== 100) {
        return <Progress value={progress} placement='inline' variant='ring' />;
      }
      const type = getMimeTypeFromFile(name);
      return <FileVisual type={getKindFromMimeType(type ?? '')} thumbnail={thumbnail} />;
    }, [progress, thumbnail, name]);

    return (
      <SummaryItem
        {...restProps}
        as={StyledFileUploadItem}
        ref={ref}
        visual={visual}
        primary={name}
        secondary={metaContent}
        actions={actions}
        error={error}
      />
    );
  }
);

export default FileUploadItem;
