import { FC, useRef, useState, useEffect, MutableRefObject, MouseEvent, useContext } from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  Button,
  StyledAvatar,
  Flex,
  Icon,
  registerIcon,
  defaultThemeProp,
  ForwardProps,
  useI18n
} from '@pega/cosmos-react-core';
import { RichTextEditorState, StyledRichTextEditor } from '@pega/cosmos-react-rte';
import * as sendIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/send.icon';
import * as paperClipIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/paper-clip.icon';

import FeedRichText from './FeedRichText';
import FeedButton from './FeedButton';
import FeedAttachments, { AttachedFiles } from './FeedAttachments';
import { FeedContext } from './Feed.context';

registerIcon(sendIcon, paperClipIcon);

export interface FeedReplyInputProps {
  /** Unique ID for this Reply Input  */
  id: string;
  /** Label text for the reply submit button. */
  commentLabel: string;
  /** A set of attachment files. */
  attachments?: AttachedFiles[];
  /** Placeholder string for the input */
  placeholder: string;
  /** A callback containing a list of added files that will run whenever files are added to the post. */
  onFilesAdded?: (files: AttachedFiles[]) => void;
  /** Callback that runs when the submit button has been pressed */
  onSubmit: (event: {
    replyId: string;
    value: string;
    attachments: AttachedFiles[];
    clear: () => void;
  }) => void;
  /** Callback that sets returns a shared ref to the RTE component */
  onSetInputRef?: (event: { ref: MutableRefObject<RichTextEditorState | undefined> }) => void;
}

export const StyledReplyInputContainer = styled.div(props => {
  const { theme } = props;

  return css`
    ${StyledAvatar} {
      margin-inline-end: ${theme.base.spacing};
    }
  `;
});

StyledReplyInputContainer.defaultProps = defaultThemeProp;

export const StyledFeedReply = styled.div(props => {
  const { theme } = props;

  return css`
    border: 0.0625rem solid ${theme.base.palette['border-line']};
    border-radius: calc(2.5 * ${theme.base['border-radius']});
    padding: 0 calc(2 * ${theme.base.spacing});
    cursor: text;
  `;
});

StyledFeedReply.defaultProps = defaultThemeProp;

const StyledFeedReplyInput = styled.div(props => {
  const { theme } = props;

  return css`
    padding-top: calc(0.5 * ${theme.base.spacing});
    padding-bottom: calc(0.5 * ${theme.base.spacing});

    ${StyledRichTextEditor} {
      &:focus-within {
        box-shadow: none;
      }
    }
  `;
});

StyledFeedReplyInput.defaultProps = defaultThemeProp;

const HiddenFileInput = styled.input`
  display: none;
`;

const ReplyFeedButton = styled.button`
  align-items: start;
`;

const FeedReplyInput: FC<ForwardProps & FeedReplyInputProps> = (
  props: ForwardProps & FeedReplyInputProps
) => {
  const rteRef = useRef<RichTextEditorState>();
  const {
    id,
    commentLabel,
    attachments = [],
    placeholder,
    onAddAttachment,
    onSubmit,
    onSetInputRef,
    onFilesAdded,
    ...restProps
  } = props;

  const [emptyText, setEmptyText] = useState(true);
  const hiddenInputRef = useRef<HTMLInputElement>(null);
  const attachmentListRef = useRef<HTMLUListElement>(null);
  const attachmentsButtonRef = useRef<HTMLButtonElement>(null);
  const submitButtonRef = useRef<HTMLButtonElement>(null);
  const { setShowSearchResults } = useContext(FeedContext);
  const { avatarSrc, fullname } = useContext(FeedContext).userInfo;

  useEffect(() => {
    onSetInputRef?.({ ref: rteRef });
  }, [rteRef]);

  const t = useI18n();

  const handleSubmit = () => {
    setShowSearchResults?.(false);
    if (!emptyText || attachments.length > 0) {
      onSubmit({
        replyId: id,
        value: rteRef.current?.getPlainText() || '',
        attachments: attachments || [],
        clear: () => {
          rteRef.current?.clear();
        }
      });
    }
  };

  return (
    <Flex container as={StyledReplyInputContainer} {...restProps}>
      <Avatar name={fullname} size='m' imageSrc={avatarSrc} />
      <Flex
        container={{ alignItems: 'start' }}
        item={{ grow: 1 }}
        as={StyledFeedReply}
        onClick={(event: MouseEvent) => {
          if (
            attachmentListRef.current &&
            event.target !== attachmentListRef.current &&
            attachmentListRef.current.contains(event.target as Node)
          ) {
            return;
          }

          if (
            attachmentsButtonRef.current?.contains(event.target as Node) ||
            submitButtonRef.current?.contains(event.target as Node)
          ) {
            return;
          }

          rteRef.current?.focus();
        }}
      >
        <Flex as={StyledFeedReplyInput} container={{ direction: 'column' }} item={{ grow: 1 }}>
          <FeedRichText
            ref={rteRef}
            placeholder={placeholder}
            onChange={() => {
              setEmptyText(rteRef.current?.getPlainText().trim() === '');
            }}
            onSubmit={handleSubmit}
          />
          <FeedAttachments ref={attachmentListRef} attachments={attachments} />
        </Flex>
        {onFilesAdded && (
          <>
            <HiddenFileInput
              ref={hiddenInputRef}
              type='file'
              multiple
              onChange={() => {
                if (hiddenInputRef.current?.files) {
                  onFilesAdded?.(Array.from(hiddenInputRef.current?.files));
                }
              }}
              // Must trick input to believe there is no value when activated so that the same file may be added consecutively.
              onClick={(event: MouseEvent) => {
                (event.target as HTMLInputElement).value = '';
              }}
            />
            <Button
              ref={attachmentsButtonRef}
              as={FeedButton}
              variant='simple'
              forwardedAs={ReplyFeedButton}
              onClick={() => {
                setShowSearchResults?.(false);
                hiddenInputRef.current?.click();
              }}
              icon
              label={t('attachment')}
            >
              <Icon name='paper-clip' />
            </Button>
          </>
        )}
        <Button
          ref={submitButtonRef}
          as={FeedButton}
          disabled={emptyText && attachments.length === 0}
          variant='simple'
          forwardedAs={ReplyFeedButton}
          onClick={handleSubmit}
          icon
          label={commentLabel}
        >
          <Icon name='send' />
        </Button>
      </Flex>
    </Flex>
  );
};

export default FeedReplyInput;
