import {
  FC,
  useRef,
  useState,
  ChangeEvent,
  useEffect,
  MouseEvent,
  KeyboardEvent as ReactKeyboardEvent,
  FormEvent
} from 'react';
import { Text, Editor, Range, Transforms } from 'slate';
import { ReactEditor, useSlate } from 'slate-react';

import {
  Button,
  CardContent,
  Grid,
  Icon,
  registerIcon,
  Input,
  Popover,
  useOuterEvent,
  useElement,
  ForwardProps,
  Form,
  useI18n
} from '@pega/cosmos-react-core';
import * as chainIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/chain.icon';

import { useRTEContext } from '../RichTextEditor.context';
import EditorCommands from '../utils/EditorCommands';

import ToolbarButton from './ToolbarButton';
import { getKeyCommand } from './utils';

registerIcon(chainIcon);

interface AnchorButtonProps {
  osx: boolean;
}

const AnchorButton: FC<AnchorButtonProps & ForwardProps> = ({ osx, ...props }) => {
  const t = useI18n();
  const { setPopoverOpen, onEditorFocus } = useRTEContext();
  const buttonRef = useRef<HTMLButtonElement>(null);
  const textInputRef = useRef<HTMLInputElement>(null);
  const urlInputRef = useRef<HTMLInputElement>(null);
  const editor = useSlate();
  const [popoverEl, setPopoverEl] = useElement();
  const [selectedText, setSelectedText] = useState('');
  const [selection, setSelection] = useState<Range | null>(null);
  const [url, setUrl] = useState('');
  const [urlMatch, setUrlMatch] = useState(true);
  const [anchorMenu, setAnchorMenu] = useState(false);
  const tooltip = getKeyCommand(osx, ({ ctrl }) => `${t('rte_anchor')} (${ctrl}K)`);

  const openMenu = (opts: { focusInput?: boolean } = {}) => {
    setAnchorMenu(true);
    setPopoverOpen(true);
    if (opts.focusInput) {
      setTimeout(() => {
        textInputRef.current?.focus();
      }, 0);
    }
  };

  const resetMenu = (opts: { focusEditor?: boolean } = { focusEditor: true }) => {
    setSelection(null);
    setSelectedText('');
    setUrl('');
    setUrlMatch(true);
    setAnchorMenu(false);
    setPopoverOpen(false);
    if (opts.focusEditor) {
      setTimeout(() => {
        onEditorFocus({
          runAfterFocus: () => {
            if (selection) Transforms.setSelection(editor, selection);
          }
        });
      }, 0);
    }
  };

  useOuterEvent('mousedown', [popoverEl], () => {
    if (anchorMenu) {
      resetMenu();
    }
  });

  const createLink = () => {
    if (url) {
      EditorCommands.createLink(
        editor,
        selectedText,
        new URL(/^[a-z][a-z0-9.+-]*:/i.test(url) ? url : `http:${url}`).href,
        selection
      );
      resetMenu();
    }
  };

  useEffect(() => {
    if (editor.selection) {
      setSelectedText(Editor.string(editor, editor.selection));
      setSelection(editor.selection);
      const [match] = Editor.nodes<Text>(editor, {
        match: n => Text.isText(n) && !!n.href,
        at: editor.selection
      });
      if (match) {
        setUrl(match[0].href || '');
        setSelectedText(match[0].text);
      } else {
        setUrl('');
      }
    }
  }, [anchorMenu]);

  const preventDef = (e: MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const isLinkActive = () => {
    const [link] = Editor.nodes<Text>(editor, { match: n => Text.isText(n) && !!n.href });
    return !!link;
  };

  const cancelAnchorCreation = (event?: KeyboardEvent | MouseEvent) => {
    event?.preventDefault();
    if (
      (event?.type === 'keydown' && (event as KeyboardEvent)?.key === 'Enter') ||
      event?.type === 'mousedown' ||
      !event
    ) {
      resetMenu({ focusEditor: false });
      buttonRef.current?.focus();
    }
  };

  useEffect(() => {
    const keyCommandListener = (e: KeyboardEvent) => {
      if (e.key === 'k' && (e.metaKey || e.ctrlKey) && ReactEditor.isFocused(editor)) {
        e.preventDefault();
        openMenu({ focusInput: true });
      }
      if (e.key === 'Escape') {
        cancelAnchorCreation();
      }
    };

    document.addEventListener('keydown', keyCommandListener);

    return () => {
      document.removeEventListener('keydown', keyCommandListener);
    };
  }, []);

  return (
    <>
      <ToolbarButton
        ref={buttonRef}
        onMouseDown={() => {
          openMenu();
        }}
        onKeyDown={(e: ReactKeyboardEvent) => {
          if (e.key === 'Enter') {
            e.preventDefault();
            openMenu({ focusInput: true });
          }
        }}
        active={isLinkActive()}
        tooltip={tooltip}
        label={t('rte_anchor')}
        {...props}
      >
        <Icon name='chain' />
      </ToolbarButton>
      <Popover show={anchorMenu} target={buttonRef.current} ref={setPopoverEl} placement='bottom'>
        <CardContent>
          <Form
            actions={
              <>
                <Button
                  variant='secondary'
                  onKeyDown={cancelAnchorCreation}
                  onMouseDown={cancelAnchorCreation}
                  type='button'
                >
                  Cancel
                </Button>
                <Button disabled={!url || !urlMatch} name='apply' type='submit' variant='primary'>
                  Apply
                </Button>
              </>
            }
            onSubmit={(e: FormEvent) => {
              e.preventDefault();
              createLink();
            }}
          >
            <Grid container={{ rowGap: 2 }}>
              <Input
                label='Text'
                value={selectedText}
                onClick={preventDef}
                onChange={(e: ChangeEvent<HTMLInputElement>) => {
                  setSelectedText(e.target.value);
                }}
                onBlur={() => {
                  setTimeout(() => {
                    if (document.activeElement !== urlInputRef.current) {
                      resetMenu({ focusEditor: false });
                    }
                  }, 0);
                }}
                ref={textInputRef}
              />
              <Input
                label='URL'
                value={url}
                onClick={preventDef}
                onChange={(e: ChangeEvent<HTMLInputElement>) => {
                  const urlInput = e.target.value;
                  setUrl(urlInput);

                  if (!urlMatch) {
                    try {
                      // eslint-disable-next-line no-new
                      new URL(
                        /^[a-z][a-z0-9+.-]*:/i.test(urlInput) ? urlInput : `http:${urlInput}`
                      );
                      setUrlMatch(true);
                    } catch {
                      setUrlMatch(false);
                    }
                  }
                }}
                onBlur={() => {
                  try {
                    // eslint-disable-next-line no-new
                    new URL(/^[a-z][a-z0-9+.-]*:/i.test(url) ? url : `http:${url}`);
                    setUrlMatch(true);
                  } catch {
                    setUrlMatch(false);
                  }
                }}
                info={!urlMatch ? 'Not a valid URL' : ''}
                status={!urlMatch ? 'error' : undefined}
                ref={urlInputRef}
              />
            </Grid>
          </Form>
        </CardContent>
      </Popover>
    </>
  );
};

export default AnchorButton;
