import { forwardRef, PropsWithoutRef, FC, Ref } from 'react';
import styled, { css, DefaultTheme } from 'styled-components';

import Icon from '../Icon';
import { ForwardProps, BaseProps, OmitStrict, PropsWithDefaults } from '../../types';
import { defaultThemeProp } from '../../theme';
import { StyledText } from '../Text';
import { ButtonProps, StyledButton } from '../Button';

import { AttachmentType } from './utils';

export interface FileVisualProps extends BaseProps {
  /** Attachment/File type, it has an impact on background color and icon rendered. */
  type: AttachmentType;
  /** A string to be used as an image src for a attachment thumbnail. */
  thumbnail?: string;
  /** Disabled means, the button is not interactive. */
  disabled?: ButtonProps['disabled'];
  /** Reference to a button element. */
  ref?: Ref<HTMLButtonElement>;
  /**
   * FileVisual size.
   * @default "s"
   */
  size?: 's' | 'l';
}
type FileVisualPropsWithDefaults = PropsWithDefaults<FileVisualProps, 'size'>;
type ThemeColor = keyof OmitStrict<DefaultTheme['base']['colors'], 'white' | 'black'>;
type ThemeColorVariant = 'extra-light' | 'light' | 'medium' | 'dark' | 'extra-dark';

type BackgroundColor = `${ThemeColor}-${ThemeColorVariant}`;

const mapping: Record<
  AttachmentType,
  { iconName?: string; backgroundColor: BackgroundColor; text?: string }
> = {
  generic: {
    iconName: 'paper-clip',
    backgroundColor: 'slate-dark'
  },
  video: {
    iconName: 'play-solid',
    backgroundColor: 'slate-dark'
  },
  audio: {
    iconName: 'speaker-on-solid',
    backgroundColor: 'slate-dark'
  },
  document: {
    iconName: 'filetype-text',
    backgroundColor: 'blue-medium'
  },
  message: {
    iconName: 'mail-solid',
    backgroundColor: 'blue-dark'
  },
  pdf: {
    text: 'PDF',
    backgroundColor: 'red-medium'
  },
  spreadsheet: {
    iconName: 'grid-solid',
    backgroundColor: 'green-medium'
  },
  presentation: {
    iconName: 'slideshow-solid',
    backgroundColor: 'orange-medium'
  },
  link: {
    iconName: 'chain',
    backgroundColor: 'purple-dark'
  },
  archive: {
    iconName: 'archive-solid',
    backgroundColor: 'slate-dark'
  },
  database: {
    iconName: 'disc-stack-solid',
    backgroundColor: 'slate-dark'
  },
  image: {
    iconName: 'picture-solid',
    backgroundColor: 'slate-dark'
  },
  text: {
    iconName: 'filetype-text',
    backgroundColor: 'slate-dark'
  }
} as const;

const sizeConverter = {
  s: 1,
  l: 1.375
};

export const StyledFileVisual = styled.div<
  Pick<FileVisualPropsWithDefaults, 'size'> & {
    thumbnail?: FileVisualProps['thumbnail'];
    aType: AttachmentType;
  }
>(({ theme, thumbnail, size, aType }) => {
  const [color, lightness] = mapping[aType].backgroundColor.split('-', 2) as [
    ThemeColor,
    ThemeColorVariant
  ];
  const backgroundColor = theme.base.colors[color];
  const sizeMultiplier = sizeConverter[size] ?? 1;
  return css`
    background-color: ${backgroundColor[lightness]};
    color: ${theme.base.palette.light};
    border-color: transparent;
    height: calc(${sizeMultiplier} * ${theme.components.input.height});
    width: calc(${sizeMultiplier} * ${theme.components.input.height});
    border-radius: calc(0.25 * ${theme.base['border-radius']});
    font-size: calc(0.7 * ${theme.base['font-size']});
    font-weight: ${theme.base['font-weight']['semi-bold']};
    overflow: hidden;
    opacity: 1;
    ${thumbnail &&
    css`
      background-image: url(${thumbnail});
      background-size: contain;
    `}

    ${StyledText} {
      color: inherit;
      white-space: nowrap;
    }
  `;
});

StyledFileVisual.defaultProps = defaultThemeProp;

const FileVisual: FC<FileVisualProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<FileVisualProps>, ref: FileVisualProps['ref']) => {
    const { type, thumbnail, size, disabled, ...restProps } = props;
    return (
      <StyledFileVisual
        {...restProps}
        as={StyledButton}
        ref={ref}
        icon
        aType={type}
        {...{
          thumbnail,
          disabled
        }}
      >
        {mapping[type].iconName && <Icon name={mapping[type].iconName!} />}
        {mapping[type].text}
      </StyledFileVisual>
    );
  }
);

export default FileVisual;
