import {
  FC,
  ReactElement,
  useState,
  useEffect,
  MutableRefObject,
  MouseEvent,
  useRef,
  useContext,
  useMemo
} from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  AvatarProps,
  Button,
  Count,
  Card,
  CardContent,
  CardHeader,
  Flex,
  Icon,
  registerIcon,
  MenuButton,
  SummaryItem,
  Text,
  defaultThemeProp,
  ForwardProps,
  useElement,
  useI18n,
  Progress,
  useOuterEvent,
  MenuItemProps,
  MetaList,
  Tooltip,
  ModalManagerContext,
  SummaryListItem,
  ModalMethods,
  StyledVisual,
  useConfiguration
} from '@pega/cosmos-react-core';
import { RichTextEditorState, StyledRichTextEditor } from '@pega/cosmos-react-rte';
import BareButton from '@pega/cosmos-react-core/lib/components/Button/BareButton';
import * as arrowMicroDownIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-down.icon';
import * as caretLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/caret-left.icon';
import { formatListToLocaleString } from '@pega/cosmos-react-core/lib/utils/utils';

import FeedButton from './FeedButton';
import FeedAttachments, { AttachedFiles } from './FeedAttachments';
import { StyledFeedReplyContainer } from './FeedReply';
import FeedContent, { FeedContentProps } from './FeedContent';
import FeedReplyInput, { FeedReplyInputProps } from './FeedReplyInput';
import { FeedContext, FeedReplyContext } from './Feed.context';
import FeedRichText from './FeedRichText';
import FeedModalList, { FeedModalListProps } from './FeedModalList';

registerIcon(arrowMicroDownIcon, caretLeftIcon);

export interface FeedPostProps {
  /** Unique ID for this activity */
  id: string;
  /** All of the information used to fill text portions, and avatar info */
  info: {
    avatarSrc?: string;
    fullname: string;
    username: string;
    timeElapsed: string;
    content: ReactElement | string;
    postType?: 'public' | 'private';
    recipients?: MenuItemProps[];
    recipientsCount?: number;
    attachments?: AttachedFiles[];
    liked?: boolean;
    icon?: string;
    postContext?: ReactElement | string;
    userStatus?: AvatarProps['status'];
  };
  maxContentHeight?: FeedContentProps['maxContentHeight'];
  /** Button labels for accessibility purposes. */
  interactionInfo: {
    likeLabel: string;
    likes?: SummaryListItem[];
    likeCount?: number;
    commentLabel: string;
    /**
     * @default true
     */
    interactionsEnabled?: boolean;
  };
  /** Callback that runs when the mouse pointer hovers over the Activity. */
  onMouseEnter?: (event: { postId: string }) => void;
  /** Callback that runs when the mouse pointer leaves the Activity. */
  onMouseLeave?: (event: { postId: string }) => void;
  /** Callback that runs when the comment button has been clicked */
  onCommentClick?: (event: { postId: string }) => void;
  /** Callback that runs when the like button has been clicked */
  onLikeClick: (event: { postId: string; user: SummaryListItem }) => void;
  /** Callback that runs when the like count on a post is hovered, focused, or clicked. */
  onLikeCountInteraction?: () => void;
  /** Callback that runs when the bottom of the extended like list Modal is reached, allowing for more likes to be loaded. */
  onLoadMoreLikes?: () => void;
  /** Callback that runs when the private recipient icon on a post is hovered, focused, or clicked. */
  onRecipientInteraction?: () => void;
  /** Callback that runs when the bottom of the extended private recipient list Modal is reached, allowing for more recipients to be loaded. */
  onLoadMoreRecipients?: () => void;
  /** Callback that runs when the user name or visual button has been clicked */
  onUserClick?: (event: { postId: string; username: string }, clickEvent?: MouseEvent) => void;
  /** MenuItems that will populate the three dot menu button */
  menuItems?: MenuItemProps[];
  /** Callback that updates the post after being edited. If undefined, the post will not be editable. */
  onEditSubmit?: (event: { postId: string; value: string }) => void;
  /**
   * Flag that indicates if a post has been previously edited.
   * @default false
   */
  edited?: boolean;
  /** Props that will be passed into the reply input, see FeedReplyInput for more information */
  replyInput: Pick<FeedReplyInputProps, 'onSubmit' | 'placeholder' | 'onFilesAdded'> & {
    commentLabel: FeedReplyInputProps['commentLabel'];
    attachments: FeedReplyInputProps['attachments'];
  };
  /** The default visibility for the reply input */
  replyInputShown?: boolean;
  /** An array to hold FeedReply's inside of this Activity */
  replies?: ReactElement[];
  /** A total count of all replies. */
  replyCount?: number;
  /** Callback that runs when the user clicks Show more button. */
  onShowMoreReplies?: (event: { postId: string }) => void;
  /** Callback that runs when the user clicks Show less button. */
  onShowLessReplies?: (event: { postId: string }) => void;
  /** A loading indicator for the replies. */
  repliesLoading?: boolean;
}

const defaultProps: Partial<FeedPostProps> = {
  info: {
    avatarSrc: '',
    fullname: '',
    username: '',
    timeElapsed: '',
    content: ''
  }
};

export const StyledFeedPost = styled.div(
  ({ theme }) => css`
    ${StyledFeedReplyContainer}:not(:last-child) {
      margin-block-end: calc(2 * ${theme.base.spacing});
    }
  `
);

StyledFeedPost.defaultProps = defaultThemeProp;

export const StyledPostEdit = styled.div(
  ({ theme }) => css`
    ${StyledRichTextEditor} {
      padding: calc(0.5 * ${theme.base.spacing});
    }
  `
);

StyledPostEdit.defaultProps = defaultThemeProp;

export const StyledReplies = styled.div(
  ({ theme }) => css`
    border-top: 0.0625rem solid ${theme.base.palette['border-line']};
    padding: calc(1.25 * ${theme.base.spacing}) calc(2 * ${theme.base.spacing});
  `
);

StyledReplies.defaultProps = defaultThemeProp;

export const StyledPostFooter = styled.div(
  ({ theme }) => css`
    margin-block-start: ${theme.base.spacing};
  `
);

StyledPostFooter.defaultProps = defaultThemeProp;

const FullWidthSummaryItem = styled.div`
  width: 100%;

  ${StyledVisual} {
    align-self: start;
  }
`;

const FeedPost: FC<ForwardProps & FeedPostProps> = (props: ForwardProps & FeedPostProps) => {
  const {
    id,
    info,
    interactionInfo,
    maxContentHeight = Infinity,
    onMouseEnter,
    onMouseLeave,
    onCommentClick,
    onLikeClick,
    onLikeCountInteraction,
    onLoadMoreLikes,
    onRecipientInteraction,
    onLoadMoreRecipients,
    onUserClick,
    onShowMoreClick,
    newReplyField,
    menuItems,
    onEditSubmit,
    edited = false,
    replies = [],
    replyInput,
    replyInputShown,
    replyCount,
    repliesLoading,
    onShowLessReplies,
    onShowMoreReplies,
    ...restProps
  } = props;
  const {
    avatarSrc,
    fullname,
    username,
    timeElapsed,
    content,
    postType = 'public',
    recipients = [],
    recipientsCount = 0,
    attachments,
    liked,
    icon,
    postContext,
    userStatus
  } = info;
  const {
    likeLabel,
    likes = [],
    likeCount = 0,
    commentLabel,
    interactionsEnabled = true
  } = interactionInfo;
  const loggedInUser = useContext(FeedContext).userInfo;

  const t = useI18n();
  const { locale } = useConfiguration();
  const [countEl, setCountEl] = useElement<HTMLButtonElement>();
  const rteRef = useRef<RichTextEditorState>();
  const [editMode, setEditMode] = useState(false);
  const editTextAreaRef = useRef<HTMLDivElement>(null);
  const [emptyEditText, setEmptyEditText] = useState(false);
  const [postModified, setPostModified] = useState(false);
  const [replyInputOpen, setReplyInputOpen] = useState(false);
  const [replyInputRef, setReplyInputRef] = useState<MutableRefObject<
    RichTextEditorState | undefined
  > | null>(null);
  const { create } = useContext(ModalManagerContext);
  const likesModalRef = useRef<ModalMethods<FeedModalListProps>>();
  const recipientsModalRef = useRef<ModalMethods<FeedModalListProps>>();

  useEffect(() => {
    likesModalRef.current?.update({
      count: likeCount,
      heading: likeLabel,
      listItems: likes,
      onLoadMore: onLoadMoreLikes
    });
  }, [likeCount, likeLabel, likes, onLoadMoreLikes]);

  useEffect(() => {
    if (recipients.length) {
      recipientsModalRef.current?.update({
        count: recipientsCount,
        heading: t('feed_recipients'),
        listItems: recipients as SummaryListItem[],
        onLoadMore: onLoadMoreRecipients
      });
    }
  }, [recipientsCount, recipients, onLoadMoreRecipients]);

  useOuterEvent('mousedown', [editTextAreaRef], () => {
    if (emptyEditText) setEditMode(false);
  });

  useEffect(() => {
    if ((replyInputOpen || replyInputShown) && replyInputRef) {
      replyInputRef.current?.focus();
    }
  }, [replyInputOpen, replyInputShown, replyInputRef]);

  const userVisual = icon ? (
    <Icon name={icon} />
  ) : (
    <Avatar name={fullname} size='l' imageSrc={avatarSrc} status={userStatus} />
  );

  const numReplies = replyCount || replies?.length;

  const postTypeAttributes = useMemo(() => {
    if (postType === 'private') {
      const recipientsList = recipients.map(x => x.primary);
      return {
        icon: 'users-solid',
        ariaLabel: `${t('feed_private_post')} t('feed_recipients')} ${formatListToLocaleString(
          recipientsList,
          t,
          locale,
          { count: recipientsCount }
        )}`,
        label: formatListToLocaleString(recipientsList, t, locale, {
          count: recipientsCount,
          separator: '\n'
        })
      };
    }

    return {
      icon: 'globe',
      ariaLabel: t('feed_public_post'),
      label: t('feed_public_post')
    };
  }, [postType, recipients, recipientsCount]);

  const likesAttributes = useMemo(() => {
    const likesList = likes.map(x => x.primary.toString());
    return {
      ariaLabel: `${t('feed_liked_by')} ${formatListToLocaleString(likesList, t, locale, {
        count: likeCount
      })}`,
      label: formatListToLocaleString(likesList, t, locale, {
        count: likeCount,
        separator: '\n'
      })
    };
  }, [likes, likeCount]);

  const handleEdit = () => {
    if (!emptyEditText && postModified) {
      onEditSubmit?.({
        postId: id,
        value: rteRef.current?.getPlainText() || ''
      });
      setEditMode(false);
    }
  };

  return (
    <FeedReplyContext.Provider value={{ onShowReplyInput: () => setReplyInputOpen(true) }}>
      <Card
        {...restProps}
        as={StyledFeedPost}
        onMouseEnter={() => {
          onMouseEnter?.({ postId: id });
        }}
        onMouseLeave={() => {
          onMouseLeave?.({ postId: id });
        }}
      >
        <CardHeader>
          <SummaryItem
            as={FullWidthSummaryItem}
            visual={
              onUserClick ? (
                <BareButton
                  onClick={(event: MouseEvent) => {
                    onUserClick?.({ postId: id, username }, event);
                  }}
                >
                  {userVisual}
                </BareButton>
              ) : (
                userVisual
              )
            }
            primary={
              <div>
                {onUserClick ? (
                  <Button
                    variant='text'
                    onClick={(event: MouseEvent) => {
                      onUserClick?.({ postId: id, username }, event);
                    }}
                  >
                    <Text variant='h3'>{fullname}</Text>
                  </Button>
                ) : (
                  <Text variant='h3'>{fullname}</Text>
                )}
                {postContext && <Text variant='primary'> {t('feed_context', [postContext])}</Text>}
              </div>
            }
            secondary={
              <Flex container={{ gap: 1, alignItems: 'center' }}>
                <Button
                  variant='text'
                  icon
                  onClick={
                    recipients.length
                      ? () => {
                          if (recipientsCount > 10) {
                            recipientsModalRef.current = create(FeedModalList, {
                              count: recipientsCount,
                              heading: t('feed_recipients'),
                              listItems: recipients as SummaryListItem[],
                              onLoadMore: onLoadMoreRecipients
                            });
                          }
                        }
                      : onRecipientInteraction
                  }
                  onFocus={onRecipientInteraction}
                  onMouseOver={onRecipientInteraction}
                  aria-label={postTypeAttributes.ariaLabel}
                  label={postTypeAttributes.label}
                >
                  <Icon name={postTypeAttributes.icon} />
                </Button>
                <MetaList items={edited ? [timeElapsed, t('edited')] : [timeElapsed]} />
              </Flex>
            }
            actions={
              menuItems ? (
                <MenuButton
                  text='More'
                  menu={{
                    items:
                      onEditSubmit &&
                      typeof content === 'string' &&
                      username === loggedInUser.username
                        ? menuItems.concat([
                            {
                              primary: t('edit'),
                              id: 'edit',
                              onClick: () => setEditMode(true)
                            }
                          ])
                        : menuItems
                  }}
                  icon='more'
                  iconOnly
                  variant='simple'
                />
              ) : undefined
            }
          />
        </CardHeader>
        <CardContent>
          {editMode ? (
            <>
              <StyledPostEdit ref={editTextAreaRef}>
                <FeedRichText
                  onChange={() => {
                    setEmptyEditText(rteRef.current?.getPlainText().trim() === '');
                    setPostModified(rteRef.current?.getPlainText() !== content);
                  }}
                  autoFocus
                  onSubmit={handleEdit}
                  defaultValue={content}
                  ref={rteRef}
                />
              </StyledPostEdit>
              <Flex container={{ justify: 'between', pad: [2, 0, 0] }}>
                <Button
                  onClick={() => setEditMode(false)}
                  aria-label={`${t('cancel')} ${t('edit')} ${id}`}
                >
                  {t('cancel')}
                </Button>
                <Button
                  variant='primary'
                  disabled={emptyEditText || !postModified}
                  onClick={handleEdit}
                  aria-label={`${t('update')} ${t('edit')} ${id}`}
                >
                  {t('update')}
                </Button>
              </Flex>
            </>
          ) : (
            <FeedContent maxContentHeight={maxContentHeight}>{content}</FeedContent>
          )}
          <FeedAttachments attachments={attachments} />
          <Flex as={StyledPostFooter} container={{ justify: 'between', wrap: 'wrap', gap: 1 }}>
            {interactionsEnabled ? (
              <Flex container={{ gap: 2 }}>
                <Button
                  variant='text'
                  as={FeedButton}
                  onClick={() => {
                    onCommentClick?.({ postId: id });
                    if (!replyInputOpen) {
                      setReplyInputOpen(true);
                    }
                  }}
                >
                  <Icon name='chat' />
                  <Text>{commentLabel}</Text>
                  {numReplies > 0 && <Count>{numReplies}</Count>}
                </Button>
                <Flex container={{ gap: 0.5 }}>
                  <Button
                    variant='text'
                    as={FeedButton}
                    onClick={() =>
                      onLikeClick({
                        postId: id,
                        user: {
                          id: loggedInUser.username,
                          primary: loggedInUser.fullname,
                          secondary: loggedInUser.meta ? (
                            <MetaList items={loggedInUser.meta} />
                          ) : undefined,
                          visual: (
                            <Avatar
                              size='l'
                              imageSrc={loggedInUser.avatarSrc}
                              name={loggedInUser.fullname}
                            />
                          )
                        }
                      })
                    }
                    emphasized={liked}
                  >
                    <Icon name={liked ? 'thumbs-up-solid' : 'thumbs-up'} />
                    <Text>{likeLabel}</Text>
                  </Button>
                  {likeCount > 0 && (
                    <>
                      <BareButton
                        ref={setCountEl}
                        onClick={
                          likes.length
                            ? () => {
                                if (likeCount > 10) {
                                  likesModalRef.current = create(FeedModalList, {
                                    count: likeCount,
                                    heading: t('feed_likes'),
                                    listItems: likes,
                                    onLoadMore: onLoadMoreLikes
                                  });
                                }
                              }
                            : onLikeCountInteraction
                        }
                        onFocus={onLikeCountInteraction}
                        onMouseOver={onLikeCountInteraction}
                        aria-label={likesAttributes.ariaLabel}
                      >
                        <Count>{likeCount}</Count>
                      </BareButton>
                      <Tooltip target={countEl} showDelay='none' hideDelay='none'>
                        {likesAttributes.label}
                      </Tooltip>
                    </>
                  )}
                </Flex>
              </Flex>
            ) : (
              <div />
            )}
            {(onShowLessReplies || onShowMoreReplies) && (
              <Flex container={{ gap: 0.5 }}>
                {replies.length !== numReplies && (
                  <>
                    <Text>{t('showing_num_of_num', [replies.length, numReplies])}</Text>
                    <span>&bull;</span>
                  </>
                )}
                {onShowMoreReplies && replies.length < numReplies && (
                  <Button variant='link' onClick={() => onShowMoreReplies({ postId: id })}>
                    {t('show_more')}
                  </Button>
                )}
                {repliesLoading && <Progress placement='inline' />}
              </Flex>
            )}
          </Flex>
        </CardContent>
        {!!replies?.length && <StyledReplies>{replies}</StyledReplies>}
        {replyInputShown || replyInputOpen ? (
          <CardContent>
            <FeedReplyInput
              onSetInputRef={({ ref }) => {
                setReplyInputRef(ref);
              }}
              id={id}
              commentLabel={replyInput.commentLabel}
              attachments={replyInput.attachments}
              placeholder={replyInput.placeholder}
              onFilesAdded={replyInput.onFilesAdded}
              onSubmit={e => {
                setReplyInputOpen(false);
                replyInput.onSubmit(e);
              }}
            />
          </CardContent>
        ) : null}
        {onShowLessReplies && !!replies.length && (
          <Flex container={{ justify: 'center', pad: [0, 0, 1], gap: 1 }}>
            <Button variant='link' onClick={() => onShowLessReplies({ postId: id })}>
              {t('show_less')}
            </Button>
            {repliesLoading && <Progress placement='inline' />}
          </Flex>
        )}
      </Card>
    </FeedReplyContext.Provider>
  );
};

FeedPost.defaultProps = defaultProps;

export default FeedPost;
