import { useState, FC, MouseEvent, useRef, useContext } from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  Button,
  CardContent,
  Flex,
  Icon,
  registerIcon,
  StyledIcon,
  StyledCard,
  StyledPopover,
  defaultThemeProp,
  useAutoResize,
  useElement,
  useOuterEvent,
  ForwardProps,
  useI18n,
  ExpandCollapse,
  useAfterInitialEffect
} from '@pega/cosmos-react-core';
import { RichTextEditorState, StyledRichTextEditor } from '@pega/cosmos-react-rte';
import * as timesIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/times.icon';
import * as paperClipIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/paper-clip.icon';

import FeedNewPostTypeMenu, {
  PostType,
  PostTypeMenuProps,
  RecipientMenuItem
} from './FeedNewPostTypeMenu';
import FeedAttachments, { AttachedFiles } from './FeedAttachments';
import FeedButton from './FeedButton';
import FeedRichText from './FeedRichText';
import { FeedContext } from './Feed.context';

registerIcon(timesIcon, paperClipIcon);

export interface FeedNewPostProps {
  /** A unique ID for the NewPost */
  id: string;
  /** A set of attachment files. */
  attachments?: AttachedFiles[];
  /** Information that is used to label buttons and inputs for accessibility purposes */
  interactionInfo: {
    textAreaPlaceholder: string;
    postLabel: string;
  };
  /** A callback containing a list of added files that will run whenever files are added to the post. */
  onFilesAdded?: (files: AttachedFiles[]) => void;
  /** A callback that will run when the new post button has been clicked */
  onPost: (event: {
    newPostId: string;
    value: string;
    attachments: AttachedFiles[];
    recipients: RecipientMenuItem[];
    username: string;
    clear: () => void;
  }) => void;
  autoFocus?: boolean;
  /** Array of private recipient options */
  recipients?: PostTypeMenuProps['recipients'];
  /** Handler for new search on private recipient options */
  onRecipientSearch?: (newSearch: string) => void;
}

export const StyledTextAreaOverflow = styled.div(({ theme }) => {
  return css`
    overflow-y: hidden;
    margin-top: ${theme.base.spacing};
    min-height: 1rem;
    width: 100%;

    ${StyledRichTextEditor} {
      &:focus-within {
        box-shadow: none;
      }
    }
  `;
});

StyledTextAreaOverflow.defaultProps = defaultThemeProp;

export const StyledFeedNewPost = styled.div(({ theme }) => {
  return css`
    ${StyledIcon}:not(${StyledPopover} ${StyledIcon}) {
      font-size: 1.25rem;
    }

    ${FeedButton} {
      margin-top: calc(0.5 * ${theme.base.spacing});
    }
  `;
});

StyledFeedNewPost.defaultProps = defaultThemeProp;

const HiddenFileInput = styled.input`
  display: none;
`;

const FeedNewPost: FC<ForwardProps & FeedNewPostProps> = (props: FeedNewPostProps) => {
  const {
    id,
    attachments = [],
    interactionInfo,
    onPost,
    onFilesAdded,
    autoFocus,
    recipients,
    onRecipientSearch,
    ...restProps
  } = props;
  const rteRef = useRef<RichTextEditorState>();
  const { textAreaPlaceholder, postLabel } = interactionInfo;
  const { setShowSearchResults } = useContext(FeedContext);
  const { avatarSrc, fullname, username } = useContext(FeedContext).userInfo;
  const [emptyText, setEmptyText] = useState(true);
  const [hasFocus, setHasFocus] = useState(autoFocus);
  const [postType, setPostType] = useState<PostType>('post');
  const [resetPostTypeMenu, setResetPostTypeMenu] = useState(false);
  const [selectedRecipients, setSelectedRecipients] = useState<RecipientMenuItem[]>([]);
  const [attachmentEl, setAttachmentEl] = useElement<HTMLInputElement>();
  const [newPostEl, setNewPostEl] = useElement<HTMLDivElement>();
  const [textAreaRef, resizeTextArea] = useAutoResize<HTMLDivElement>();

  useOuterEvent('mousedown', [newPostEl], () => {
    if (emptyText) rteRef.current?.clear();
  });

  const t = useI18n();

  const handlePost = () => {
    setShowSearchResults?.(false);
    if ((!emptyText || attachments.length > 0) && attachments.every(x => !x.error)) {
      onPost({
        newPostId: id,
        value: rteRef.current?.getPlainText() || '',
        attachments: attachments || [],
        recipients: selectedRecipients,
        username,
        clear: () => {
          rteRef.current?.clear();
          setResetPostTypeMenu(true);
        }
      });
    }
  };

  const handleClear = () => {
    setShowSearchResults?.(false);
    rteRef.current?.clear();
  };

  useAfterInitialEffect(() => {
    if (postType === 'post') {
      rteRef.current?.focus();
    }
    if (postType !== 'private') {
      setSelectedRecipients([] as RecipientMenuItem[]);
    }
  }, [postType]);

  return (
    <StyledCard ref={setNewPostEl} as={StyledFeedNewPost} {...restProps}>
      <CardContent>
        {onRecipientSearch && (
          <FeedNewPostTypeMenu
            recipients={recipients || ([] as PostTypeMenuProps['recipients'])}
            onSetPostType={setPostType}
            onSelectedRecipientsUpdate={setSelectedRecipients}
            onRecipientSearch={onRecipientSearch}
            reset={resetPostTypeMenu}
            onReset={() => setResetPostTypeMenu(false)}
          />
        )}

        <Flex container={{ alignItems: 'start', gap: 1 }}>
          <Avatar name={fullname} size='l' imageSrc={avatarSrc} />

          <StyledTextAreaOverflow ref={textAreaRef}>
            <FeedRichText
              label={t('feed_new_post')}
              labelHidden
              onChange={() => {
                setEmptyText(rteRef.current?.getPlainText().trim() === '');
                resizeTextArea();
              }}
              placeholder={textAreaPlaceholder}
              autoFocus={autoFocus}
              onSubmit={handlePost}
              onFocus={() => setHasFocus(true)}
              onBlur={() => setHasFocus(false)}
              ref={rteRef}
            />
          </StyledTextAreaOverflow>

          {!emptyText && (
            <Button
              variant='simple'
              as={FeedButton}
              onClick={handleClear}
              icon
              label={t('feed_cancel_conversation')}
            >
              <Icon name='times' />
            </Button>
          )}

          {onFilesAdded && (
            <Button
              variant='simple'
              as={FeedButton}
              onClick={() => {
                setShowSearchResults?.(false);
                attachmentEl?.click();
              }}
              icon
              label={t('attachment')}
            >
              <Icon name='paper-clip' />
            </Button>
          )}
        </Flex>

        <HiddenFileInput
          ref={setAttachmentEl}
          type='file'
          multiple
          onChange={() => {
            if (attachmentEl?.files) {
              onFilesAdded?.(Array.from(attachmentEl?.files));
            }
          }}
          // Must trick input to believe there is no value when activated so that the same file may be added consecutively.
          onClick={(event: MouseEvent) => {
            (event.target as HTMLInputElement).value = '';
          }}
        />

        <FeedAttachments attachments={attachments} />

        <ExpandCollapse collapsed={!hasFocus && emptyText && !attachments.length}>
          <Flex container={{ justify: 'end' }}>
            <Button
              variant='primary'
              disabled={(emptyText && attachments.length === 0) || attachments.some(x => x.error)}
              onClick={handlePost}
            >
              {postLabel}
            </Button>
          </Flex>
        </ExpandCollapse>
      </CardContent>
    </StyledCard>
  );
};

export default FeedNewPost;
