import {
  forwardRef,
  PropsWithoutRef,
  useState,
  useRef,
  useMemo,
  FC,
  useEffect,
  useCallback,
  MouseEvent,
  KeyboardEvent,
  useImperativeHandle,
  ClipboardEvent,
  FocusEvent,
  useReducer
} from 'react';
// Import the Slate editor factory.
import { createEditor, Editor, Node, Element, Text, Range, Transforms } from 'slate';
// Import the Slate components and React plugin.
import { Slate, Editable, withReact, ReactEditor } from 'slate-react';
import { withHistory } from 'slate-history';
import styled from 'styled-components';
import { VirtualElement } from '@popperjs/core';

import {
  FormField,
  FormControl,
  StyledPopover,
  useTheme,
  useUID,
  createUID,
  debounce,
  ForwardProps,
  Popover,
  Menu,
  menuHelpers,
  MenuItemProps,
  MenuProps
} from '@pega/cosmos-react-core';

import StyledRichTextEditor, { EditorContainer, StyledFormField } from './RichTextEditor.styles';
import Toolbar from './Toolbar/Toolbar';
import { nodeRenderer, leafRenderer } from './utils/renderers';
import { onSearchChange } from './utils/search';
import EditorCommands from './utils/EditorCommands';
import ListCommands from './utils/ListCommands';
import { convertHtml } from './utils/htmlConverter';
import { convertSlateToHtml } from './utils/slateConverter';
import { RichTextEditorState, RichTextEditorProps } from './RichTextEditor.types';
import { RichTextEditorContext, RichTextEditorContextValue } from './RichTextEditor.context';
import { convertMarkdownForEditor } from './utils/markdownConverter';

type SyntheticActionType = 'prev' | 'next' | 'child' | 'parent' | 'list_change' | 'set' | 'clear';

const withVoids = (editor: Editor): ReactEditor => {
  editor.isVoid = element => {
    if (element.type === 'image' || element.type === 'image-placeholder') {
      return true;
    }
    return false;
  };

  return editor as ReactEditor;
};

const syntheticFocusReducer = (
  currentId: string | undefined,
  action: {
    type: SyntheticActionType;
    menuItems: MenuProps['items'];
    selectedItemId?: MenuItemProps['id'];
  }
): string | undefined => {
  const { type, menuItems, selectedItemId } = action;
  switch (type) {
    case 'child':
      // return first item id if top-level
      if (!currentId) return menuItems[0].id;
      // if currently highlighted item has children, select first of them
      return menuHelpers.getItem(menuItems, currentId)?.items?.[0].id;
    case 'parent':
      // find and return parent of currently selected item
      return menuHelpers.getParentItem(menuItems, currentId)?.id;
    case 'next':
      // return next sibling of currently selected item
      return menuHelpers.getNextItem(menuItems, currentId)?.id ?? currentId;
    case 'prev':
      // return prev sibling of currently selected item
      return menuHelpers.getPrevItem(menuItems, currentId)?.id ?? currentId;
    case 'list_change':
      // if currently selected item does not exist in new list, select first one
      if (currentId && menuHelpers.getItem(menuItems, currentId)) return currentId;
      return menuItems[0]?.id;
    case 'set':
      // return arbitrary selected item id if it is in the tree; undefined otherwise
      return selectedItemId ? menuHelpers.getItem(menuItems, selectedItemId)?.id : undefined;
    case 'clear':
      return undefined;
    default:
      return currentId;
  }
};

const StyledSearchPopover = styled(StyledPopover)`
  min-width: 20rem;
`;

const RichTextEditor: FC<RichTextEditorProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<RichTextEditorProps>, ref: RichTextEditorProps['ref']) => {
    const {
      components: {
        'text-area': { 'min-height': minHeight }
      }
    } = useTheme();
    const uid = useUID();
    const {
      id = uid,
      label,
      labelHidden,
      info,
      status,
      required,
      disabled,
      readOnly,
      onChange,
      onFocus,
      onBlur,
      placeholder,
      toolbar,
      onImageAdded,
      markdownOnly,
      searchTriggers,
      onSearch,
      getSearchItemReplacement,
      searchRenderers,
      menu,
      height = { min: minHeight },
      defaultValue,
      autoFocus,
      ...restProps
    } = props;

    const formFieldRef = useRef<HTMLElement>(null);
    const editor = useMemo(() => withVoids(withReact(withHistory(createEditor()))), []);
    const [value, setValue] = useState<(Element | Text)[]>(() =>
      defaultValue && !markdownOnly
        ? convertHtml(defaultValue)
        : convertMarkdownForEditor(defaultValue, searchRenderers)
    );
    const [toolbarPopover, setToolbarPopover] = useState(false);
    const [toolbarSticky, setToolbarSticky] = useState(false);
    const [focusableToolbarBtn, setFocusableToolbarBtn] = useState<HTMLElement | null>(null);
    const [focusedId, dispatchFocus] = useReducer(syntheticFocusReducer, undefined);

    const [popoverTarget, setPopoverTarget] = useState<Range | null>(null);
    const [searchEl, setSearchEl] = useState<VirtualElement | null>(null);

    const updateToolbarStyle = () => {
      if ((formFieldRef.current?.offsetHeight || 0) > window.innerHeight) {
        setToolbarSticky(true);
      } else {
        setToolbarSticky(false);
      }
    };

    const debouncedUpdateToolbarStyle = debounce(updateToolbarStyle, 200);

    const getPlainText = () => {
      return value
        .map(node => {
          if (markdownOnly && (node as Element).children) {
            return (node as Element).children
              .map(textNode => (textNode as Text).searchResult?.markdown || (textNode as Text).text)
              .join('');
          }
          return Node.string(node);
        })
        .join('\n');
    };

    const onEditorFocus: RichTextEditorContextValue['onEditorFocus'] = ({
      event,
      force,
      focusEditor,
      runAfterFocus
    }) => {
      if (focusEditor) {
        setTimeout(() => {
          if (!ReactEditor.isFocused(editor)) {
            ReactEditor.focus(editor);
            onFocus?.();
          }
        }, 0);
      }

      if (runAfterFocus && (force || (!disabled && !toolbarPopover))) {
        setTimeout(runAfterFocus, 0);
      } else {
        event?.stopPropagation();
      }
    };

    const blurEditor = () => {
      if (ReactEditor.isFocused(editor)) {
        ReactEditor.blur(editor);
        onBlur?.();
      }
    };

    useImperativeHandle<any, RichTextEditorState>(
      ref,
      () => ({
        focus: () => {
          onEditorFocus({ focusEditor: true });
        },
        getPlainText: () => {
          return getPlainText();
        },
        getRichText: () => {
          return JSON.stringify(value);
        },
        getHtml: () => {
          return convertSlateToHtml(value);
        },
        clear: () => {
          const point = { path: [0, 0], offset: 0 };
          editor.selection = { anchor: point, focus: point };
          setValue([
            {
              type: 'paragraph',
              children: [{ text: '' }]
            }
          ]);
        },
        appendImage: ({ src, alt }, imageId) => {
          const path = EditorCommands.appendImage(editor, { src, alt }, imageId);
          if (path) {
            Transforms.select(editor, { anchor: path, focus: path });
            ReactEditor.focus(editor);
          }
        },
        insertText: (text: string) => {
          editor.insertText(text);
        },
        insertHtml: (html: string, overwrite: boolean = false) => {
          setValue(prevValue => {
            if (overwrite) {
              const point = { path: [0, 0], offset: 0 };
              editor.selection = { anchor: point, focus: point };
              return convertHtml(html !== '' ? html : '<p></p>');
            }
            return [...prevValue, ...convertHtml(html)];
          });
        }
      }),
      [value, setValue]
    );

    const onMenuItemClick = (itemId: string, e: MouseEvent<any> | KeyboardEvent<any>) => {
      // MenuItem expects actual click events, but we don't have reference to actual menu item element to trigger a click.
      menu?.onItemClick?.(itemId, e as MouseEvent<any>);
      const replacement = getSearchItemReplacement?.(itemId);
      if (replacement) {
        let node: Node;

        if (replacement.markdown && replacement.component && replacement.props) {
          node = {
            text: replacement.text,
            searchResult: {
              markdown: replacement.markdown,
              component: replacement.component,
              props: replacement.props,
              // Prevents copying searchResult attribute onPaste
              toJSON: () => undefined
            }
          };
        } else {
          node = { text: replacement.text };
        }

        EditorCommands.replaceWithNodes(editor, popoverTarget, node);
      }
    };

    const onKeyDown = useCallback(
      (event: KeyboardEvent<any>) => {
        if (popoverTarget && menu) {
          switch (event.key) {
            case 'ArrowDown':
              event.preventDefault();
              dispatchFocus({ type: 'next', menuItems: menu.items, selectedItemId: focusedId });
              break;
            case 'ArrowUp':
              event.preventDefault();
              dispatchFocus({ type: 'prev', menuItems: menu.items, selectedItemId: focusedId });
              break;
            case 'Enter':
              event.preventDefault();
              if (focusedId) {
                onMenuItemClick(focusedId, event);
                dispatchFocus({ type: 'clear', menuItems: menu.items });
              }
              break;
            case 'Escape':
              event.preventDefault();
              setPopoverTarget(null);
              break;
            default:
              break;
          }
        } else if (event.key === 'Tab' && !event.shiftKey) {
          if (focusableToolbarBtn) {
            focusableToolbarBtn.focus();
            event.preventDefault();
          }
        } else {
          ListCommands.checkListEnter(editor, event);
          EditorCommands.checkLinkBreak(editor, event);
          EditorCommands.checkSearchDownKeys(editor, event);
          EditorCommands.checkImageEnter(editor, event);
          EditorCommands.checkKeyCommands(editor, event);
        }
      },
      [menu, focusedId, popoverTarget, focusableToolbarBtn]
    );

    const onKeyUp = (event: KeyboardEvent<any>) => {
      EditorCommands.checkSearchUpKeys(editor, event);
    };

    // Adds an image placeholder to be replaced later in time by an image
    const addImage = (file: File) => {
      const placeholderId = EditorCommands.appendImagePlaceholder(editor);
      if (placeholderId) onImageAdded?.(file, placeholderId);
    };

    const onPaste = (e: ClipboardEvent) => {
      let image;
      for (let i = 0; i < e.clipboardData.items.length; i += 1) {
        const item = e.clipboardData.items[i];

        if (item.type.startsWith('image')) {
          image = item.getAsFile();
        }
      }

      if (image) {
        e.preventDefault();
        addImage(image);
      }
    };

    const onDrop = (e: DragEvent) => {
      e.preventDefault();
      if (e.dataTransfer) {
        Array.from(e.dataTransfer.files).forEach(file => {
          if (file.type.includes('image')) {
            addImage(file);
          }
        });
      }
    };

    useEffect(() => {
      if (popoverTarget) {
        setSearchEl(ReactEditor.toDOMRange(editor, popoverTarget) as VirtualElement);
      }
    }, [popoverTarget]);

    useEffect(() => {
      onChange?.();
    }, [value]);

    useEffect(() => {
      if (menu && popoverTarget) {
        dispatchFocus({
          type: 'set',
          menuItems: menu.items,
          selectedItemId: menu.items[0]?.id
        });
      }
    }, [menu, popoverTarget]);

    useEffect(() => {
      if (autoFocus) {
        setTimeout(() => {
          ReactEditor.focus(editor);
        }, 0);
      }
    }, []);

    return (
      <RichTextEditorContext.Provider
        value={{
          addImage,
          setPopoverOpen: setToolbarPopover,
          onEditorFocus,
          disabled: disabled || false
        }}
      >
        <FormField
          as={StyledFormField}
          toolbar={!!(!markdownOnly && toolbar?.length)}
          label={
            // eslint-disable-next-line jsx-a11y/no-static-element-interactions, jsx-a11y/click-events-have-key-events
            <span onClick={() => onEditorFocus({ focusEditor: true })}>{label}</span>
          }
          labelHidden={labelHidden}
          id={id}
          info={info}
          status={status}
          required={required}
          disabled={disabled}
          readOnly={readOnly}
          onDrop={onDrop}
          onFocus={(e: FocusEvent) => {
            if (e.target === formFieldRef.current) {
              onEditorFocus({ focusEditor: true });
            }
          }}
          onBlur={blurEditor}
          ref={formFieldRef}
          aria-label={`${label} ${
            !markdownOnly && !readOnly && toolbar?.length ? 'Hit tab to edit toolbar below' : ''
          }`}
        >
          <Slate
            editor={editor}
            value={value}
            onChange={newValue => {
              if (!disabled) {
                setValue(newValue);
                debouncedUpdateToolbarStyle();

                if (searchTriggers && onSearch && markdownOnly) {
                  const res = searchTriggers.flatMap(({ trigger, regex }) => {
                    for (let i = 0; i < 10; i += 1) {
                      try {
                        const groupId = createUID();
                        return [
                          {
                            trigger,
                            groupId: `g${groupId}`,
                            regex: new RegExp(
                              String.raw`(?:^|\s)(?<match>${trigger}(?<g${groupId}>${
                                regex || String.raw`\w*`
                              }))$`
                            )
                          }
                        ];
                      } catch (e: unknown) {
                        if (!(e instanceof SyntaxError)) throw e;
                      }
                    }

                    // Failsafe
                    return [];
                  });

                  let search;
                  for (let i = 0; i < res.length; i += 1) {
                    search = onSearchChange(editor, res[i]);
                    if (search) break;
                  }

                  if (search) {
                    setPopoverTarget(search.target);
                    onSearch({ search: search.search, trigger: search.trigger });
                  } else {
                    setPopoverTarget(null);
                  }
                }
              }
            }}
          >
            <FormControl
              {...restProps}
              ref={ref}
              ariaLabel={label}
              id={id}
              required={required}
              disabled={disabled}
              readOnly={readOnly}
              status={status}
              as={StyledRichTextEditor}
              toolbar={!!(!markdownOnly && toolbar?.length)}
              onClick={() => {
                onEditorFocus({ focusEditor: true });
              }}
              focused={ReactEditor.isFocused(editor)}
            >
              <EditorContainer
                toolbar={!!(!markdownOnly && toolbar?.length)}
                containerHeight={height}
                onClick={() => {
                  if (!disabled) {
                    onEditorFocus({ focusEditor: true });
                  }
                }}
              >
                <Editable
                  renderElement={nodeRenderer}
                  renderLeaf={leafRenderer}
                  onKeyDown={onKeyDown}
                  onKeyUp={onKeyUp}
                  placeholder={placeholder}
                  onFocus={event => {
                    if (!disabled) {
                      onEditorFocus({ focusEditor: true, event });
                    } else {
                      blurEditor();
                    }
                  }}
                  onBlur={blurEditor}
                  disabled={disabled}
                  readOnly={readOnly}
                  onPaste={onPaste}
                />
              </EditorContainer>
              {!markdownOnly && !readOnly && toolbar?.length ? (
                <Toolbar
                  features={toolbar}
                  sticky={toolbarSticky}
                  setFocusableBtn={el => {
                    setFocusableToolbarBtn(el);
                  }}
                />
              ) : null}
            </FormControl>
            <Popover
              show={!!popoverTarget && !!menu}
              strategy='fixed'
              target={searchEl}
              placement='bottom-start'
            >
              {menu && (
                <Menu
                  {...menu}
                  as={StyledSearchPopover}
                  currentItemId={focusedId}
                  mode='action'
                  onItemClick={onMenuItemClick}
                />
              )}
            </Popover>
          </Slate>
        </FormField>
      </RichTextEditorContext.Provider>
    );
  }
);

export default RichTextEditor;
