import {
  forwardRef,
  FunctionComponent,
  isValidElement,
  PropsWithoutRef,
  ReactElement,
  Ref,
  useContext,
  useRef,
  useState
} from 'react';
import styled from 'styled-components';

import {
  Button,
  ForwardProps,
  Popover,
  useConsolidatedRef,
  useOuterEvent
} from '@pega/cosmos-react-core';

import { FeedContext } from '../Feed/Feed.context';

interface MentionButtonProps {
  /** Text for the button, or a regex match containing all necessary props. */
  children: RegExpExecArray | ReactElement;
  /** The id of the mention.  */
  id?: string;
  /** The text of the mention button. */
  text?: string;
  /** Designates the type of the mention for proper handling on click. */
  type?: string;
}

const StyledButton = styled(Button)`
  &:enabled:focus,
  &:not([disabled]):focus {
    box-shadow: none;
    text-decoration: underline;
  }
`;

const MentionButton: FunctionComponent<MentionButtonProps & ForwardProps> = forwardRef(
  (
    {
      children,
      id: idProp = '',
      text: textProp = '',
      type: typeProp = '',
      ...restProps
    }: PropsWithoutRef<MentionButtonProps>,
    ref: Ref<HTMLButtonElement>
  ) => {
    const { onMentionClick } = useContext(FeedContext);
    const [showPopover, setShowPopover] = useState(false);
    const [mentionContent, setMentionContent] = useState<JSX.Element | undefined>();
    const mentionRef = useConsolidatedRef(ref);
    const popoverRef = useRef(null);

    useOuterEvent(['click', 'focusin'], [mentionRef, popoverRef], () => {
      setShowPopover(false);
    });

    let leadingChar = '';
    let trailingChar = '';
    let id = '';
    let text = '';
    let type = '';

    if (!isValidElement(children)) {
      if (!children.groups) return <span>{children[0]}</span>;

      [leadingChar, trailingChar] = children[0].split(children.groups.match);
      id = children.groups.id;
      text = children.groups.text;
      type = children.groups.type;
    } else {
      id = idProp;
      text = textProp;
      type = typeProp;
    }

    const handleClick = () => {
      const comp = onMentionClick?.({
        id,
        text,
        type
      });
      if (comp) setShowPopover(true);
      setMentionContent(comp);
    };

    return (
      <>
        {leadingChar && <span>{leadingChar}</span>}
        <StyledButton
          {...restProps}
          ref={mentionRef}
          variant='link'
          onClick={handleClick}
          spellCheck={false}
        >
          {isValidElement(children) ? children : text}
        </StyledButton>
        {trailingChar && <span>{trailingChar}</span>}
        <Popover
          ref={popoverRef}
          show={showPopover}
          target={mentionRef.current}
          placement='bottom-start'
          arrow
        >
          {mentionContent}
        </Popover>
      </>
    );
  }
);

export const MentionButtonConfig = {
  type: 'mention',
  regexPattern: /(?:^|)(?<match>@(?<id>[^ :]+\w):(?<text>[^:]+\w):(?<type>[^:]+\w):)/g,
  component: MentionButton
};

export default MentionButton;
