import {
  FC,
  ReactElement,
  useContext,
  MouseEvent,
  useState,
  useRef,
  useEffect,
  useMemo
} from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  AvatarProps,
  Count,
  Button,
  Flex,
  Icon,
  registerIcon,
  MenuButton,
  defaultThemeProp,
  ForwardProps,
  MenuItemProps,
  MetaList,
  useI18n,
  useOuterEvent,
  Tooltip,
  useElement,
  SummaryListItem,
  ModalMethods,
  ModalManagerContext,
  useConfiguration
} from '@pega/cosmos-react-core';
import { StyledAvatar } from '@pega/cosmos-react-core/lib/components/Avatar/Avatar';
import BareButton from '@pega/cosmos-react-core/lib/components/Button/BareButton';
import { RichTextEditorState, StyledRichTextEditor } from '@pega/cosmos-react-rte';
import * as chatIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/chat.icon';
import * as thumbsUpSolidIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/thumbs-up-solid.icon';
import * as thumbsUpIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/thumbs-up.icon';
import { formatListToLocaleString } from '@pega/cosmos-react-core/lib/utils/utils';

import FeedAttachments, { AttachedFiles } from './FeedAttachments';
import FeedButton from './FeedButton';
import FeedContent from './FeedContent';
import { FeedContext, FeedReplyContext } from './Feed.context';
import FeedRichText from './FeedRichText';
import { StyledFeedReply } from './FeedReplyInput';
import FeedModalList, { FeedModalListProps } from './FeedModalList';

registerIcon(chatIcon, thumbsUpSolidIcon, thumbsUpIcon);

export interface FeedReplyProps {
  id: string;
  info: {
    avatarSrc?: string;
    timeElapsed: string;
    content: ReactElement | string;
    attachments?: AttachedFiles[];
    username: string;
    fullname: string;
    fullnameLabel: string;
    liked?: boolean;
    icon?: string;
    userStatus?: AvatarProps['status'];
  };
  interactionInfo: {
    likes?: SummaryListItem[];
    likeCount?: number;
    likeLabel: string;
    commentLabel: string;
    /**
     * @default true
     */
    interactionsEnabled?: boolean;
  };
  /**
   * A maximum height in pixels to display post content within before prompting to expand the post.
   * @default Infinity
   */
  maxContentHeight?: number;
  /** A set of menu options for actions on the reply. */
  menuItems?: MenuItemProps[];
  /** Callback that updates the reply after being edited. If undefined, the reply will not be editable. */
  onEditSubmit?: (event: { replyId: string; value: string }) => void;
  /**
   * Flag that indicates if a reply has been previously edited.
   * @default false
   */
  edited?: boolean;
  onMouseEnter?: (event: { replyId: string }) => void;
  onMouseLeave?: (event: { replyId: string }) => void;
  onCommentClick: (event: { replyId: string; username: string }) => void;
  onLikeClick: (event: { replyId: string; user: SummaryListItem }) => void;
  /** Callback that runs when the like count on a post is hovered, focused, or clicked. */
  onLikeCountInteraction?: () => void;
  /** Callback that runs when the bottom of the extended like list Modal is reached, allowing for more likes to be loaded. */
  onLoadMoreLikes?: () => void;
  onUserClick?: (event: { replyId: string; username: string }, clickEvent?: MouseEvent) => void;
}
export const StyledFeedReplyContent = styled.div(props => {
  const { theme } = props;

  return css`
    background-color: ${theme.base.palette['secondary-background']};
    border-radius: calc(2 * ${theme.base['border-radius']});
    padding: calc(1.5 * ${theme.base.spacing});
  `;
});

StyledFeedReplyContent.defaultProps = defaultThemeProp;

export const StyledFeedReplyUser = styled.button(props => {
  const { theme } = props;

  return css`
    font-weight: ${theme.base['font-weight']['semi-bold']};
    margin-inline-end: ${theme.base.spacing};
  `;
});

StyledFeedReplyUser.defaultProps = defaultThemeProp;

export const StyledVisualButton = styled.button`
  height: min-content;
`;

export const StyledFeedReplyContainer = styled.div(props => {
  const { theme } = props;

  return css`
    ${StyledAvatar} {
      margin-inline-end: ${theme.base.spacing};
    }

    ${StyledFeedReplyContent} {
      margin-bottom: calc(0.5 * ${theme.base.spacing});
    }

    ${StyledFeedReply} {
      &:focus-within {
        border-color: ${theme.base.palette.interactive};
      }
    }

    ${StyledRichTextEditor} {
      padding-top: calc(0.5 * ${theme.base.spacing});
      padding-bottom: ${theme.base.spacing};

      &:focus-within {
        box-shadow: none;
      }
    }
  `;
});

StyledFeedReplyContainer.defaultProps = defaultThemeProp;

const FeedReply: FC<ForwardProps & FeedReplyProps> = (props: ForwardProps & FeedReplyProps) => {
  const {
    id,
    info,
    interactionInfo,
    maxContentHeight = Infinity,
    menuItems,
    onEditSubmit,
    edited = false,
    onMouseEnter,
    onMouseLeave,
    onCommentClick,
    onLikeClick,
    onLikeCountInteraction,
    onLoadMoreLikes,
    onUserClick,
    ...restProps
  } = props;
  const {
    avatarSrc,
    timeElapsed,
    content,
    attachments,
    username,
    fullname,
    fullnameLabel,
    liked,
    icon,
    userStatus
  } = info;
  const {
    likes = [],
    likeCount = 0,
    likeLabel,
    commentLabel,
    interactionsEnabled = true
  } = interactionInfo;
  const loggedInUser = useContext(FeedContext).userInfo;

  const t = useI18n();
  const { locale } = useConfiguration();
  const [countEl, setCountEl] = useElement<HTMLButtonElement>();
  const rteRef = useRef<RichTextEditorState>();
  const [editMode, setEditMode] = useState(false);
  const editTextAreaRef = useRef<HTMLDivElement>(null);
  const [emptyEditText, setEmptyEditText] = useState(false);
  const [replyModified, setReplyModified] = useState(false);
  const { onShowReplyInput } = useContext(FeedReplyContext);
  const { create } = useContext(ModalManagerContext);
  const likesModalRef = useRef<ModalMethods<FeedModalListProps>>();

  useEffect(() => {
    likesModalRef.current?.update({
      count: likeCount,
      heading: likeLabel,
      listItems: likes,
      onLoadMore: onLoadMoreLikes
    });
  }, [likeCount, likeLabel, likes, onLoadMoreLikes]);

  useOuterEvent('mousedown', [editTextAreaRef], () => {
    if (emptyEditText) setEditMode(false);
  });

  const likesAttributes = useMemo(() => {
    const likesList = likes.map(x => x.primary.toString());
    return {
      ariaLabel: `${t('feed_liked_by')} ${formatListToLocaleString(likesList, t, locale, {
        count: likeCount
      })}`,
      label: formatListToLocaleString(likesList, t, locale, {
        count: likeCount,
        separator: '\n'
      })
    };
  }, [likes, likeCount]);

  const handleEdit = () => {
    if (!emptyEditText && replyModified) {
      onEditSubmit?.({
        replyId: id,
        value: rteRef.current?.getPlainText() || ''
      });
      setEditMode(false);
    }
  };

  const userVisual = icon ? (
    <Icon name={icon} />
  ) : (
    <Avatar name={fullname} size='m' imageSrc={avatarSrc} status={userStatus} />
  );

  return (
    <Flex
      container
      as={StyledFeedReplyContainer}
      onMouseEnter={() => {
        onMouseEnter?.({ replyId: id });
      }}
      onMouseLeave={() => {
        onMouseLeave?.({ replyId: id });
      }}
      {...restProps}
    >
      {onUserClick ? (
        <BareButton
          as={StyledVisualButton}
          onClick={(event: MouseEvent) => {
            onUserClick?.({ replyId: id, username }, event);
          }}
        >
          {userVisual}
        </BareButton>
      ) : (
        userVisual
      )}
      <Flex container={{ direction: 'column' }} item={{ grow: 1 }}>
        {editMode ? (
          <>
            <StyledFeedReply>
              <FeedRichText
                onChange={() => {
                  setEmptyEditText(rteRef.current?.getPlainText().trim() === '');
                  setReplyModified(rteRef.current?.getPlainText() !== content);
                }}
                autoFocus
                onSubmit={handleEdit}
                defaultValue={content}
                ref={rteRef}
              />
            </StyledFeedReply>
            <Flex container={{ justify: 'between', pad: [2, 0, 1] }}>
              <Button
                onClick={() => setEditMode(false)}
                aria-label={`${t('cancel')} ${t('edit')} ${id}`}
              >
                {t('cancel')}
              </Button>
              <Button
                variant='primary'
                disabled={emptyEditText || !replyModified}
                onClick={handleEdit}
                aria-label={`${t('update')} ${t('edit')} ${id}`}
              >
                {t('update')}
              </Button>
            </Flex>
          </>
        ) : (
          <Flex container={{ gap: 1, alignItems: 'center' }} item={{ grow: 1 }}>
            <Flex item={{ grow: 1 }} as={StyledFeedReplyContent}>
              <Flex container={{ direction: 'column', gap: 0.5 }}>
                <div>
                  <Button
                    as={StyledFeedReplyUser}
                    variant='text'
                    onClick={(event: MouseEvent) => {
                      onUserClick?.({ replyId: id, username }, event);
                    }}
                    aria-label={fullnameLabel}
                  >
                    {fullname}
                  </Button>
                </div>
                <FeedContent maxContentHeight={maxContentHeight}>{content}</FeedContent>
              </Flex>
              <FeedAttachments attachments={attachments} />
            </Flex>
            {menuItems ? (
              <MenuButton
                text='More'
                menu={{
                  items:
                    onEditSubmit &&
                    typeof content === 'string' &&
                    username === loggedInUser.username
                      ? menuItems.concat([
                          {
                            primary: t('edit'),
                            id: 'edit',
                            onClick: () => setEditMode(true)
                          }
                        ])
                      : menuItems
                }}
                icon='more'
                iconOnly
                variant='simple'
              />
            ) : undefined}
          </Flex>
        )}

        <Flex container={{ alignItems: 'center', gap: 2 }}>
          {interactionsEnabled && (
            <>
              <Button
                variant='text'
                icon
                as={FeedButton}
                onClick={() => {
                  onCommentClick({ replyId: id, username });
                  onShowReplyInput?.();
                }}
                label={commentLabel}
              >
                <Icon name='chat' />
              </Button>
              <Flex container={{ itemGap: 0.5 }}>
                <Button
                  as={FeedButton}
                  variant='text'
                  onClick={() =>
                    onLikeClick({
                      replyId: id,
                      user: {
                        id: loggedInUser.username,
                        primary: loggedInUser.fullname,
                        secondary: loggedInUser.meta ? (
                          <MetaList items={loggedInUser.meta} />
                        ) : undefined,
                        visual: (
                          <Avatar
                            size='l'
                            imageSrc={loggedInUser.avatarSrc}
                            name={loggedInUser.fullname}
                          />
                        )
                      }
                    })
                  }
                  emphasized={liked}
                  label={likeLabel}
                  icon
                >
                  <Icon name={liked ? 'thumbs-up-solid' : 'thumbs-up'} />
                </Button>
                {likeCount > 0 && (
                  <>
                    <BareButton
                      ref={setCountEl}
                      onClick={
                        likes.length
                          ? () => {
                              if (likeCount > 10) {
                                likesModalRef.current = create(FeedModalList, {
                                  count: likeCount,
                                  heading: t('feed_likes'),
                                  listItems: likes,
                                  onLoadMore: onLoadMoreLikes
                                });
                              }
                            }
                          : onLikeCountInteraction
                      }
                      onFocus={onLikeCountInteraction}
                      onMouseOver={onLikeCountInteraction}
                      aria-label={likesAttributes.ariaLabel}
                    >
                      <Count>{likeCount}</Count>
                    </BareButton>
                    <Tooltip target={countEl} showDelay='none' hideDelay='none'>
                      {likesAttributes.label}
                    </Tooltip>
                  </>
                )}
              </Flex>
            </>
          )}
          <MetaList items={edited ? [timeElapsed, t('edited')] : [timeElapsed]} />
        </Flex>
      </Flex>
    </Flex>
  );
};

export default FeedReply;
