import {
  forwardRef,
  FunctionComponent,
  isValidElement,
  PropsWithoutRef,
  ReactElement,
  Ref,
  useContext
} from 'react';
import styled from 'styled-components';

import { Tag, ForwardProps } from '@pega/cosmos-react-core';

import { FeedContext } from '../Feed/Feed.context';

interface HashtagButtonProps {
  /** Text for the button, or a regex match containing all necessary props. */
  children: RegExpExecArray | ReactElement;
  /** The tag value. */
  tag?: string;
}

const StyledTag = styled(Tag)`
  cursor: pointer;
`;

const HashtagButton: FunctionComponent<HashtagButtonProps & ForwardProps> = forwardRef(
  (
    { children, tag: tagProp = '', ...restProps }: PropsWithoutRef<HashtagButtonProps>,
    ref: Ref<HTMLButtonElement>
  ) => {
    const { onTagClick } = useContext(FeedContext);

    let leadingChar = '';
    let trailingChar = '';
    let tag = '';

    if (!isValidElement(children)) {
      if (!children.groups) return <span>{children[0]}</span>;

      [leadingChar, trailingChar] = children[0].split(children.groups.match);
      tag = children.groups.tag;
    } else {
      tag = tagProp;
    }

    return (
      <>
        {leadingChar && <span>{leadingChar}</span>}
        <StyledTag {...restProps} ref={ref} onClick={() => onTagClick?.(tag)} spellCheck={false}>
          {isValidElement(children) ? children : tag}
        </StyledTag>
        {trailingChar && <span>{trailingChar}</span>}
      </>
    );
  }
);

export const HashtagButtonConfig = {
  type: 'tag',
  regexPattern: /(?:^|)(?<match>#(?<tag>[^ :]+\w):)/g,
  component: HashtagButton
};

export default HashtagButton;
