import styled, { css } from 'styled-components';

import { defaultThemeProp } from '@pega/cosmos-react-core';

const FeedButton = styled.button<{ emphasized?: boolean; icon?: boolean }>(props => {
  const { emphasized, icon, theme } = props;

  return css`
    color: ${emphasized ? theme.base.palette.interactive : theme.base.palette['foreground-color']};
    vertical-align: top;

    span {
      margin-inline-start: calc(0.5 * ${theme.base.spacing});
    }

    ${icon &&
    css`
      display: inline-flex;
      flex-direction: row;
      align-items: center;
    `}
  `;
});

FeedButton.defaultProps = defaultThemeProp;

export default FeedButton;
